@media (min-width: 0) and (max-width: 500px) {
	div.name {
		font-size: 7vh;
	}

	.hiddenOnMobile {
		display: none;
	}

	div.visibleOnMobile {
		display: block;
	}
}

.profile-container {
	position: relative;
	justify-content: center;
	align-items: center;
	background: #005165;
	height: 100vh;
	max-height: 50vh;
	overflow: hidden;
	// max-width: 1400px;
	margin: 0 auto
}

.name {
	color: white;
	font-size: 10vh;
	position: absolute;
	z-index: 100;
	right: 5vw;
	bottom: 5vh;
	text-align: right;
	font-family: 'Signika Negative', sans-serif;
}

.project__links,
.project__links:active,
.project__links:visited {
	color: white;
	text-shadow: none;
	z-index: 100;
	text-decoration: none;
}

.links {
	position: absolute;
	z-index: 100;
	top: 5vh;
	right: 5vw;
	width: 120px;
	display: flex;
	justify-content: space-between;
	font-family: 'Urbanist', sans-serif;
	font-size: 1.2rem;
}

.project__links:hover {
	color: #EEF1FF;
	// transform: scale(1.2);
	text-shadow: 0px 0px 25px #ffc000;
}

.visibleOnMobile {
	display: none;
}

