}


.triangle {
	top: 0;
	right: 0;
	position: absolute;
	// padding: 0px 100px;
	z-index: 0;
	border-style: solid;
	border-width: 0px 0px 100vh 80vw;
	border-color: transparent transparent #b1b2ff transparent;
	overflow: hidden;
}