.description {

	max-width: 1400px;
	display: flex;
	justify-content: space-around;
	width: 80vw;
	margin: 50px auto 30px;
	color: #ffc000;
	flex-wrap: wrap;
	align-items: center;


	&-block, &-picture {
		display: flex;
		align-items: center;
		// height: 100px;
		padding: 10px;
	}

	&-image {
		width: 100%;
		height: 15vh;
		border-radius: 100%;
		border: 2px solid #005165;
		// box-shadow: 0em 0em 7px 3px #005165;
	}
}